body {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: 'Roboto Condensed', sans-serif;
}

#wr_page_title {
    text-align: center;
}

#country_list {
    list-style-type: none;
    column-count: 3;
    max-width: 1000px;
    border-radius: 4px;
    border: 1px solid lightgray;
    background-color: #FAFAFA;
    padding: 30px;
}

#world-map {
    width: 1000px;
}

.flag {
    width: 20px;
    margin: 0 10px 0 0;
    display: inline-block;
}

.population {
    margin: 0 0 12px 0;
    font-size: 10px;
}

.blue {
    color: #5575C1;
}

.green {
    color: #4C834B;
}

.red {
    color: #D24E5B;
}

.yellow {
    color: #FFD435;
}

.purple {
    color: #A653BA;
}

.default {
    color: grey;
}